import React from 'react'
import { connect } from 'react-redux'

export class CurrentWorkout extends React.Component {
  render() {
    console.log(this.props)
    return (
      <div>
        <h3> Current Workout </h3>
        {this.props.currentWorkout ? (
          <table>
            <tr>
              <th>Exercise name</th>
              <th>Sets</th>
              <th>Reps</th>
              <th>Weight</th>
            </tr>
            {this.props.currentWorkout.map(exercise => (
              <tr>
                <td>{exercise.name}</td>
                <td>{exercise.sets}</td>
                <td>{exercise.reps}</td>
                <td>{exercise.weight}kg</td>
              </tr>
            ))}
          </table>
        ) : null}
      </div>
    )
  }
}

const mapStateToProps = ({ workouts: { currentWorkout } }) => ({
  currentWorkout,
})

export default connect(mapStateToProps)(CurrentWorkout)
